

























import { Component, Vue } from 'vue-property-decorator';
import { getMusicArticleList } from '../api';
import { Loading, LoadMore } from '../components';

@Component({
  components: {
    Loading,
    LoadMore,
  },
})
export default class MusicArticle extends Vue {

  private page: number = 1;

  private datas: object[] = [];

  private created() {
    this.loadData();
  }

  private toDetail(id: number): void {
    this.$router.push({path: `/music-article/detail/${id}`});
  }

  private async loadData(): Promise<void> {
    const res = await getMusicArticleList({
      page: this.page,
      size: 10,
      category: 4,
    });
    if (this.page === 1) {
      this.datas = res;
    } else {
      this.datas = [...this.datas, ...res];
    }
  }

  private async topMethod(end: () => void) {
    this.page = 1;
    await this.loadData();
    end();
  }

  private async bottomMethod(end: () => void) {
    this.page++;
    await this.loadData();
    end();
  }
}
